
@use '../util' as *;
.card-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    grid-template-rows: repeat(2, auto);

    justify-items: start;

    gap: rem(23);


    .card__subtitle{

    }

    .card__count,
    .card__change{
        margin-bottom: 0;
        align-self: end;

    }

    img, 
    .card__change{
        justify-self: end;
    }

}